button.jtlitem-modal-open {
  padding: .4rem .8rem;
  order: 3;
}
.t-Form-fieldContainer--large button.jtlitem-modal-open.a-Button {
    padding: .8rem .9rem;
}
.t-Form-fieldContainer--xlarge button.jtlitem-modal-open.a-Button {
    padding: 1.2rem;
}


.t-Form-fieldContainer--floatingLabel .t-Form-itemWrapper {
    align-items: stretch;
    width: 100%;
}

.jtlitem-content {
  margin: 0 10px;
}
.jtlitem-content table {
  margin: 10px 0;
}
.jtlitem-content table tr.selected {
  outline: 1px dashed orange;
}
.jtlitem-content table tr.selected td{
  background-color: #f7f7ba;
}
.jtlitem-button-container .ui-icon {
  display: inline-block;
}
.jtlitem-button-container {
  padding: 5px 10px;
  position: relative;
  float: right;
}

